<template>
  <div class="detail">
    <div class="status" :class="activeName==='jobs'?'grayBg':''">
      <div class="bg">

      </div>
      <div class="container">
            <div class='company'>
                <img :src="com.comPhotoUrl" alt="">
                <p class="companyName">{{com.comName}}</p>
                <p class="companyText">{{getPeople(com.comNumPeople)}} l {{com.industry}}</p>
            </div>
            <div class="tab">
                <el-tabs v-model="activeName" @tab-click="handleClick">
                  <bread :data="[{name:'首页',url:'/'},{name:'职位招聘',url:''},{name:'专业不限'.name,url:''}]"></bread>
                  <el-tab-pane label="公司简介" name="company"><companyInfo :data="data" :com="com"></companyInfo></el-tab-pane>
                  <el-tab-pane label="职位招聘" name="jobs" style="text-align:left;">
                    <div class="container">
                      <jobList :list="list" :total="total"></jobList>
                      <otherInfo :list="list"></otherInfo>
                    </div>
                  </el-tab-pane>
                </el-tabs>
            </div>
      </div>
    </div>
    <!-- <companyInfo></companyInfo> -->
  </div>
</template>
<script>
import api from '../api'
import companyInfo from './compoments/companyInfo'
import jobList from './compoments/jobList'
import otherInfo from './compoments/otherInfo'
import bread from './compoments/bread'
export default {
  name:'CompanyDetail',
  components:{
    companyInfo,
    jobList,
    otherInfo,
    bread
  },
  data(){
    return{
      list:[],
      data:{},
      com:{},
      total:0,
      activeName:'company'
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    getPeople(val){
      console.log(val)
      let newVal
      switch(val){
        case 0:
        newVal = '20以下'
        break;
        case 1:
        newVal = '20-150人'
        break;
        case 2:
        newVal = '150-500人'
        break;
        case 3:
        newVal = '500-999人'
        break;
        case 4:
        newVal = '1000人以上'
        break;
        default:
        break;
      }
      return newVal;

    },
    handleClick(el){
      console.log(el)
    },
   async init(){
     await api.get('http://47.112.170.125:8080/env/position/getPositionById/'+this.$route.query.id
      ).then(res=>{
        this.data = res
         api.post('http://47.112.170.125:8080/env/company/getByIdOrUserId?id='+res.comId,
          {}
          ).then(resp=>{
            this.com = resp;
          })
        api.post('http://47.112.170.125:8080/env/position/getListByComId?id='+res.comId,
          ).then(responese=>{
            this.list = responese.rows
            this.total = responese.total
          })
      })
    },
  },
}
</script>
<style lang="less">
.detail{
  .grayBg{
    background-color:#f6f6f8
  }
  .bread{
    text-align: left;
  }
  .status{
     background-color: #fff;
     height: auto;
    position: relative;
    .bg{
      position: absolute;
      background:url('../assets/img/companyBg.jpg');
      background-size:100% 100%;
      height: 180px;
      width: 100%;
    }
    .company{
      position: relative;
      overflow: hidden;
      text-align: left;
      margin-top: 10px;
      img{
        width: 120px;
        float: left;
        height: 120px;
        margin-right: 20px;
      }
      .companyName{
        color:#fff;
        font-size: 18px;
        font-weight: 400;
        padding: 20px 0;
      }
      .companyText{
        color:#fff;
      }
    }
    .tab{
      position: relative;
      z-index: 999;
      .el-tabs__header{
        margin-top: 10px;
        .el-tabs__nav-wrap::after{
          background-color: transparent;
        }
        .el-tabs__item{
          color:#fff;
        }
      }
    }
  }
}
  
</style>


